import React, { Component } from 'react';
import './about.css';

export default class About extends Component {

    render() {
        return (
            <div className="aboutDiv">
                <div className="titleDiv">
                    <a name="aboutTarget">
                        <h3 id="aboutHeader">
                            About
                        </h3>
                    </a>
                </div>
            </div>
        )
    }

}